import { RAStream } from "./RAStream";

/**
 *     lock/unlock     .
 * @param stream
 * @param onLock ,      /
 */
export const streamLock = async <Res>(
  stream: RAStream,
  onLock: (stream: RAStream) => Promise<Res>
): Promise<Res> => {
  try {
    await stream.lock();
    return await onLock(stream);
  } finally {
    stream.unlock();
  }
};
